import React from 'react';
import { default as MuiInputLabel, InputLabelProps } from '@mui/material/InputLabel';

const InputLabel = React.forwardRef<HTMLElement, InputLabelProps>( ( props, ref ) => {
	return (
		<MuiInputLabel
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<InputLabelProps>;

export default InputLabel;
