import React from 'react';
import { default as MuiRadioGroup, RadioGroupProps } from '@mui/material/RadioGroup';

const RadioGroup = React.forwardRef<HTMLElement, RadioGroupProps>( ( props, ref ) => {
	return (
		<MuiRadioGroup
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<RadioGroupProps>;

export default RadioGroup;
