import React from 'react';
import { default as MuiToggleButtonGroup, ToggleButtonGroupProps } from '@mui/material/ToggleButtonGroup';

const ToggleButtonGroup = React.forwardRef<HTMLElement, ToggleButtonGroupProps>( ( props, ref ) => {
	return (
		<MuiToggleButtonGroup
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ToggleButtonGroupProps>;

export default ToggleButtonGroup;
