import React from 'react';
import { default as MuiFab, FabProps } from '@mui/material/Fab';

const Fab = React.forwardRef<HTMLElement, FabProps>( ( props, ref ) => {
	return (
		<MuiFab
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FabProps>;

export default Fab;
