import React from 'react';
import { default as MuiListItemIcon, ListItemIconProps } from '@mui/material/ListItemIcon';

const ListItemIcon = React.forwardRef<HTMLElement, ListItemIconProps>( ( props, ref ) => {
	return (
		<MuiListItemIcon
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ListItemIconProps>;

export default ListItemIcon;
