import React from 'react';
import { default as MuiSwipeableDrawer, SwipeableDrawerProps } from '@mui/material/SwipeableDrawer';

const SwipeableDrawer = React.forwardRef<HTMLElement, SwipeableDrawerProps>( ( props, ref ) => {
	return (
		<MuiSwipeableDrawer
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SwipeableDrawerProps>;

export default SwipeableDrawer;
