import React from 'react';
import { default as MuiFilledInput, FilledInputProps } from '@mui/material/FilledInput';

const FilledInput = React.forwardRef<HTMLElement, FilledInputProps>( ( props, ref ) => {
	return (
		<MuiFilledInput
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FilledInputProps>;

export default FilledInput;
