import React from 'react';
import { default as MuiAutocomplete, AutocompleteProps } from '@mui/material/Autocomplete';

// Modified reason: the AutocompleteProps is a generic that requires additional types.
// eslint-disable-next-line @typescript-eslint/no-explicit-any
type Props = AutocompleteProps<any, boolean, boolean, boolean, any>;

const Autocomplete = React.forwardRef<HTMLElement, Props>( ( props, ref ) => {
	return (
		<MuiAutocomplete
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<Props>;

export default Autocomplete;
