import React from 'react';
import { default as MuiSnackbar, SnackbarProps } from '@mui/material/Snackbar';

const Snackbar = React.forwardRef<HTMLElement, SnackbarProps>( ( props, ref ) => {
	return (
		<MuiSnackbar
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SnackbarProps>;

export default Snackbar;
