import React from 'react';
import { default as MuiDialogTitle, DialogTitleProps } from '@mui/material/DialogTitle';

// Modified reason: modifying the default props.
const DialogTitle = React.forwardRef<HTMLElement, DialogTitleProps>( ( props, ref ) => {
	return (
		<MuiDialogTitle
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<DialogTitleProps>;

DialogTitle.defaultProps = {
	variant: 'subtitle1',
};

export default DialogTitle;
