import React from 'react';
import { default as MuiImageListItemBar, ImageListItemBarProps } from '@mui/material/ImageListItemBar';

const ImageListItemBar = React.forwardRef<HTMLElement, ImageListItemBarProps>( ( props, ref ) => {
	return (
		<MuiImageListItemBar
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ImageListItemBarProps>;

export default ImageListItemBar;
