import React from 'react';
import { default as MuiCircularProgress, CircularProgressProps } from '@mui/material/CircularProgress';

const CircularProgress = React.forwardRef<HTMLElement, CircularProgressProps>( ( props, ref ) => {
	return (
		<MuiCircularProgress
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CircularProgressProps>;

export default CircularProgress;
