import React from 'react';
import { default as MuiAlertTitle, AlertTitleProps } from '@mui/material/AlertTitle';

const AlertTitle = React.forwardRef<HTMLElement, AlertTitleProps>( ( props, ref ) => {
	return (
		<MuiAlertTitle
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AlertTitleProps>;

export default AlertTitle;
