import React from 'react';
import { default as MuiSwitch, SwitchProps } from '@mui/material/Switch';

const Switch = React.forwardRef<HTMLElement, SwitchProps>( ( props, ref ) => {
	return (
		<MuiSwitch
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SwitchProps>;

export default Switch;
