import React from 'react';
import { default as MuiOutlinedInput, OutlinedInputProps } from '@mui/material/OutlinedInput';

const OutlinedInput = React.forwardRef<HTMLElement, OutlinedInputProps>( ( props, ref ) => {
	return (
		<MuiOutlinedInput
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<OutlinedInputProps>;

export default OutlinedInput;
