import React from 'react';
import { default as MuiTableHead, TableHeadProps } from '@mui/material/TableHead';

const TableHead = React.forwardRef<HTMLElement, TableHeadProps>( ( props, ref ) => {
	return (
		<MuiTableHead
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableHeadProps>;

export default TableHead;
