import React from 'react';
import { default as MuiCardContent, CardContentProps } from '@mui/material/CardContent';

const CardContent = React.forwardRef<HTMLElement, CardContentProps>( ( props, ref ) => {
	return (
		<MuiCardContent
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CardContentProps>;

export default CardContent;
