import React from 'react';
import { default as MuiAccordionDetails, AccordionDetailsProps } from '@mui/material/AccordionDetails';

const AccordionDetails = React.forwardRef<HTMLElement, AccordionDetailsProps>( ( props, ref ) => {
	return (
		<MuiAccordionDetails
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AccordionDetailsProps>;

export default AccordionDetails;
