import React from 'react';
import { default as MuiTablePagination, TablePaginationProps } from '@mui/material/TablePagination';

const TablePagination = React.forwardRef<HTMLElement, TablePaginationProps>( ( props, ref ) => {
	return (
		<MuiTablePagination
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TablePaginationProps>;

export default TablePagination;
