import React from 'react';
import { default as MuiTable, TableProps } from '@mui/material/Table';

const Table = React.forwardRef<HTMLElement, TableProps>( ( props, ref ) => {
	return (
		<MuiTable
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableProps>;

export default Table;
