import React from 'react';
import { default as MuiAvatarGroup, AvatarGroupProps } from '@mui/material/AvatarGroup';

const AvatarGroup = React.forwardRef<HTMLElement, AvatarGroupProps>( ( props, ref ) => {
	return (
		<MuiAvatarGroup
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AvatarGroupProps>;

export default AvatarGroup;
