import React from 'react';
import clsx from 'clsx';
import Box, { BoxProps } from '@elementor/ui/Box';
import { styled, useThemeProps, createSlots } from '@elementor/ui/styles';
import './DialogHeaderGroup.types';

export interface DialogHeaderGroupProps extends BoxProps {
	/** Equivalent to margin-left: auto (and disables the default left-margin of the group) */
	gutterLeftAuto?: boolean;
	/** Equivalent to margin-right: auto */
	gutterRightAuto?: boolean;
	/** Disable the gap between the component children. */
	disableSpacing?: boolean;
	/** Disable the default left-margin value between the current DialogHeaderGroup and the previous one. */
	disableGutters?: boolean;
}

const { slots, classNames } = createSlots( 'DialogHeaderGroup', [ 'root' ] );

export type DialogHeaderGroupSlots = keyof typeof slots;

const Root = styled( Box, slots.root )<{ ownerState: DialogHeaderGroupProps }>( ( { theme, ownerState } ) => {
	const { disableSpacing, disableGutters, gutterLeftAuto, gutterRightAuto } = ownerState;

	return {
		display: 'flex',
		alignItems: 'center',
		gap: disableSpacing ? undefined : theme.spacing( 1 ),
		'.MuiDialogHeaderGroup-root + &.MuiDialogHeaderGroup-root': ( disableGutters || gutterLeftAuto ) ? undefined : {
			marginLeft: theme.spacing( 2 ),
		},
		marginLeft: gutterLeftAuto ? 'auto' : undefined,
		marginRight: gutterRightAuto ? 'auto' : undefined,
	};
} );

const DialogHeaderGroup = React.forwardRef<HTMLDivElement, DialogHeaderGroupProps>( ( inProps, ref ) => {
	const props = useThemeProps( { props: inProps, name: slots.root.name } );

	const { disableSpacing, disableGutters, gutterLeftAuto, gutterRightAuto, ...rootProps } = props;

	const ownerState = {
		disableSpacing,
		disableGutters,
		gutterLeftAuto,
		gutterRightAuto,
	};

	return (
		<Root
			{ ...rootProps }
			ref={ ref }
			className={ clsx( [ [ classNames.root, rootProps.className ] ] ) }
			ownerState={ ownerState }
		/>
	);
} ) as React.FC<DialogHeaderGroupProps>;

export default DialogHeaderGroup;
