import React from 'react';
import { default as MuiStepButton, StepButtonProps } from '@mui/material/StepButton';

const StepButton = React.forwardRef<HTMLElement, StepButtonProps>( ( props, ref ) => {
	return (
		<MuiStepButton
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StepButtonProps>;

export default StepButton;
