import React from 'react';
import { default as MuiImageList, ImageListProps } from '@mui/material/ImageList';

const ImageList = React.forwardRef<HTMLElement, ImageListProps>( ( props, ref ) => {
	return (
		<MuiImageList
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ImageListProps>;

export default ImageList;
