import React from 'react';
import { default as MuiListSubheader, ListSubheaderProps } from '@mui/material/ListSubheader';

const ListSubheader = React.forwardRef<HTMLElement, ListSubheaderProps>( ( props, ref ) => {
	return (
		<MuiListSubheader
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ListSubheaderProps>;

export default ListSubheader;
