import React from 'react';
import { default as MuiFade, FadeProps } from '@mui/material/Fade';

const Fade = React.forwardRef<HTMLElement, FadeProps>( ( props, ref ) => {
	return (
		<MuiFade
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FadeProps>;

export default Fade;
