import React from 'react';
import { default as MuiFormHelperText, FormHelperTextProps } from '@mui/material/FormHelperText';

const FormHelperText = React.forwardRef<HTMLElement, FormHelperTextProps>( ( props, ref ) => {
	return (
		<MuiFormHelperText
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FormHelperTextProps>;

export default FormHelperText;
