import React from 'react';
import { default as MuiSpeedDial, SpeedDialProps } from '@mui/material/SpeedDial';

const SpeedDial = React.forwardRef<HTMLElement, SpeedDialProps>( ( props, ref ) => {
	return (
		<MuiSpeedDial
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SpeedDialProps>;

export default SpeedDial;
