import React from 'react';
import { default as MuiTabScrollButton, TabScrollButtonProps } from '@mui/material/TabScrollButton';

const TabScrollButton = React.forwardRef<HTMLElement, TabScrollButtonProps>( ( props, ref ) => {
	return (
		<MuiTabScrollButton
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TabScrollButtonProps>;

export default TabScrollButton;
