import React from 'react';
import { default as MuiMobileStepper, MobileStepperProps } from '@mui/material/MobileStepper';

const MobileStepper = React.forwardRef<HTMLElement, MobileStepperProps>( ( props, ref ) => {
	return (
		<MuiMobileStepper
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<MobileStepperProps>;

export default MobileStepper;
