import React from 'react';
import { default as MuiInputBase, InputBaseProps } from '@mui/material/InputBase';

const InputBase = React.forwardRef<HTMLElement, InputBaseProps>( ( props, ref ) => {
	return (
		<MuiInputBase
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<InputBaseProps>;

export default InputBase;
