import React from 'react';
import { default as MuiAccordionActions, AccordionActionsProps } from '@mui/material/AccordionActions';

const AccordionActions = React.forwardRef<HTMLElement, AccordionActionsProps>( ( props, ref ) => {
	return (
		<MuiAccordionActions
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<AccordionActionsProps>;

export default AccordionActions;
