import React from 'react';
import { default as MuiSpeedDialIcon, SpeedDialIconProps } from '@mui/material/SpeedDialIcon';

const SpeedDialIcon = React.forwardRef<HTMLElement, SpeedDialIconProps>( ( props, ref ) => {
	return (
		<MuiSpeedDialIcon
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SpeedDialIconProps>;

export default SpeedDialIcon;
