import React from 'react';
import { default as MuiFormControlLabel, FormControlLabelProps } from '@mui/material/FormControlLabel';

const FormControlLabel = React.forwardRef<HTMLElement, FormControlLabelProps>( ( props, ref ) => {
	return (
		<MuiFormControlLabel
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FormControlLabelProps>;

export default FormControlLabel;
