import React from 'react';
import { default as MuiCollapse, CollapseProps } from '@mui/material/Collapse';

const Collapse = React.forwardRef<HTMLElement, CollapseProps>( ( props, ref ) => {
	return (
		<MuiCollapse
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CollapseProps>;

export default Collapse;
