import React from 'react';
import { default as MuiFormGroup, FormGroupProps } from '@mui/material/FormGroup';

const FormGroup = React.forwardRef<HTMLElement, FormGroupProps>( ( props, ref ) => {
	return (
		<MuiFormGroup
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FormGroupProps>;

export default FormGroup;
