import React from 'react';
import { default as MuiDivider, DividerProps } from '@mui/material/Divider';

const Divider = React.forwardRef<HTMLElement, DividerProps>( ( props, ref ) => {
	return (
		<MuiDivider
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<DividerProps>;

export default Divider;
