import React from 'react';
import { default as MuiStepContent, StepContentProps } from '@mui/material/StepContent';

const StepContent = React.forwardRef<HTMLElement, StepContentProps>( ( props, ref ) => {
	return (
		<MuiStepContent
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StepContentProps>;

export default StepContent;
