import React from 'react';
import { default as MuiTableBody, TableBodyProps } from '@mui/material/TableBody';

const TableBody = React.forwardRef<HTMLElement, TableBodyProps>( ( props, ref ) => {
	return (
		<MuiTableBody
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableBodyProps>;

export default TableBody;
