import React from 'react';
import { default as MuiRating, RatingProps } from '@mui/material/Rating';

const Rating = React.forwardRef<HTMLElement, RatingProps>( ( props, ref ) => {
	return (
		<MuiRating
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<RatingProps>;

export default Rating;
