import React from 'react';
import { default as MuiFormLabel, FormLabelProps } from '@mui/material/FormLabel';

const FormLabel = React.forwardRef<HTMLElement, FormLabelProps>( ( props, ref ) => {
	return (
		<MuiFormLabel
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<FormLabelProps>;

export default FormLabel;
