import React from 'react';
import { default as MuiListItemText, ListItemTextProps } from '@mui/material/ListItemText';

const ListItemText = React.forwardRef<HTMLElement, ListItemTextProps>( ( props, ref ) => {
	return (
		<MuiListItemText
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ListItemTextProps>;

export default ListItemText;
