import { JSXElementConstructor } from 'react';
import { styled, CreateMUIStyled } from '@elementor/ui/styles';

// eslint-disable-next-line @typescript-eslint/no-explicit-any
const withDirection = <C extends Parameters<CreateMUIStyled>[0] | JSXElementConstructor<any>, >( Component: C ) => {
	// eslint-disable-next-line @typescript-eslint/ban-ts-comment
	// @ts-ignore
	return styled( Component )( ( { theme } ) => {
		return {
			// Flip the icon in RTL mode.
			transform: theme.direction === 'rtl' ? 'scaleX(-1)' : undefined,
		};
	} );
};

export default withDirection;
