import React from 'react';
import { default as MuiImageListItem, ImageListItemProps } from '@mui/material/ImageListItem';

const ImageListItem = React.forwardRef<HTMLElement, ImageListItemProps>( ( props, ref ) => {
	return (
		<MuiImageListItem
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ImageListItemProps>;

export default ImageListItem;
