import React from 'react';
import { default as MuiTableCell, TableCellProps } from '@mui/material/TableCell';

const TableCell = React.forwardRef<HTMLElement, TableCellProps>( ( props, ref ) => {
	return (
		<MuiTableCell
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableCellProps>;

export default TableCell;
