import React from 'react';
import { default as MuiCardHeader, CardHeaderProps } from '@mui/material/CardHeader';

const CardHeader = React.forwardRef<HTMLElement, CardHeaderProps>( ( props, ref ) => {
	return (
		<MuiCardHeader
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CardHeaderProps>;

export default CardHeader;
