import React from 'react';
import { default as MuiPaginationItem, PaginationItemProps } from '@mui/material/PaginationItem';

const PaginationItem = React.forwardRef<HTMLElement, PaginationItemProps>( ( props, ref ) => {
	return (
		<MuiPaginationItem
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<PaginationItemProps>;

export default PaginationItem;
