import React from 'react';
import { default as MuiTableFooter, TableFooterProps } from '@mui/material/TableFooter';

const TableFooter = React.forwardRef<HTMLElement, TableFooterProps>( ( props, ref ) => {
	return (
		<MuiTableFooter
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableFooterProps>;

export default TableFooter;
