import React from 'react';
import { default as MuiSlide, SlideProps } from '@mui/material/Slide';

const Slide = React.forwardRef<HTMLElement, SlideProps>( ( props, ref ) => {
	return (
		<MuiSlide
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SlideProps>;

export default Slide;
