import React from 'react';
import { default as MuiCheckbox, CheckboxProps } from '@mui/material/Checkbox';

const Checkbox = React.forwardRef<HTMLElement, CheckboxProps>( ( props, ref ) => {
	return (
		<MuiCheckbox
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CheckboxProps>;

export default Checkbox;
