import React from 'react';
import { default as MuiCard, CardProps } from '@mui/material/Card';

const Card = React.forwardRef<HTMLElement, CardProps>( ( props, ref ) => {
	return (
		<MuiCard
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CardProps>;

export default Card;
