import React from 'react';
import { default as MuiGrow, GrowProps } from '@mui/material/Grow';

const Grow = React.forwardRef<HTMLElement, GrowProps>( ( props, ref ) => {
	return (
		<MuiGrow
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<GrowProps>;

export default Grow;
