import React from 'react';
import { default as MuiIcon, IconProps } from '@mui/material/Icon';

const Icon = React.forwardRef<HTMLElement, IconProps>( ( props, ref ) => {
	return (
		<MuiIcon
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<IconProps>;

export default Icon;
