import React from 'react';
import { default as MuiListItemSecondaryAction, ListItemSecondaryActionProps } from '@mui/material/ListItemSecondaryAction';

const ListItemSecondaryAction = React.forwardRef<HTMLElement, ListItemSecondaryActionProps>( ( props, ref ) => {
	return (
		<MuiListItemSecondaryAction
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ListItemSecondaryActionProps>;

export default ListItemSecondaryAction;
