import React from 'react';
import { default as MuiNativeSelect, NativeSelectProps } from '@mui/material/NativeSelect';

const NativeSelect = React.forwardRef<HTMLElement, NativeSelectProps>( ( props, ref ) => {
	return (
		<MuiNativeSelect
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<NativeSelectProps>;

export default NativeSelect;
