import React from 'react';
import { default as MuiTab, TabProps } from '@mui/material/Tab';

const Tab = React.forwardRef<HTMLElement, TabProps>( ( props, ref ) => {
	return (
		<MuiTab
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TabProps>;

export default Tab;
