import React from 'react';
import { default as MuiBreadcrumbs, BreadcrumbsProps } from '@mui/material/Breadcrumbs';

const Breadcrumbs = React.forwardRef<HTMLElement, BreadcrumbsProps>( ( props, ref ) => {
	return (
		<MuiBreadcrumbs
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<BreadcrumbsProps>;

export default Breadcrumbs;
