import React from 'react';
import { default as MuiCardActions, CardActionsProps } from '@mui/material/CardActions';

const CardActions = React.forwardRef<HTMLElement, CardActionsProps>( ( props, ref ) => {
	return (
		<MuiCardActions
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<CardActionsProps>;

export default CardActions;
