import React from 'react';
import { default as MuiTabs, TabsProps } from '@mui/material/Tabs';

const Tabs = React.forwardRef<HTMLElement, TabsProps>( ( props, ref ) => {
	return (
		<MuiTabs
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TabsProps>;

export default Tabs;
