import React from 'react';
import { default as MuiTableRow, TableRowProps } from '@mui/material/TableRow';

const TableRow = React.forwardRef<HTMLElement, TableRowProps>( ( props, ref ) => {
	return (
		<MuiTableRow
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableRowProps>;

export default TableRow;
