import React from 'react';
import { default as MuiSpeedDialAction, SpeedDialActionProps } from '@mui/material/SpeedDialAction';

const SpeedDialAction = React.forwardRef<HTMLElement, SpeedDialActionProps>( ( props, ref ) => {
	return (
		<MuiSpeedDialAction
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SpeedDialActionProps>;

export default SpeedDialAction;
