import React from 'react';
import { default as MuiBottomNavigationAction, BottomNavigationActionProps } from '@mui/material/BottomNavigationAction';

const BottomNavigationAction = React.forwardRef<HTMLElement, BottomNavigationActionProps>( ( props, ref ) => {
	return (
		<MuiBottomNavigationAction
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<BottomNavigationActionProps>;

export default BottomNavigationAction;
