import React from 'react';
import { default as MuiTableSortLabel, TableSortLabelProps } from '@mui/material/TableSortLabel';

const TableSortLabel = React.forwardRef<HTMLElement, TableSortLabelProps>( ( props, ref ) => {
	return (
		<MuiTableSortLabel
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableSortLabelProps>;

export default TableSortLabel;
