import React from 'react';
import { default as MuiPagination, PaginationProps } from '@mui/material/Pagination';

const Pagination = React.forwardRef<HTMLElement, PaginationProps>( ( props, ref ) => {
	return (
		<MuiPagination
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<PaginationProps>;

export default Pagination;
