import React from 'react';
import { default as MuiBottomNavigation, BottomNavigationProps } from '@mui/material/BottomNavigation';

const BottomNavigation = React.forwardRef<HTMLElement, BottomNavigationProps>( ( props, ref ) => {
	return (
		<MuiBottomNavigation
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<BottomNavigationProps>;

export default BottomNavigation;
