import React from 'react';
import { default as MuiGrid, GridProps } from '@mui/material/Grid';

const Grid = React.forwardRef<HTMLElement, GridProps>( ( props, ref ) => {
	return (
		<MuiGrid
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<GridProps>;

export default Grid;
