import React from 'react';
import { default as MuiBox, BoxProps } from '@mui/material/Box';

const Box = React.forwardRef<HTMLElement, BoxProps>( ( props, ref ) => {
	return (
		<MuiBox
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<BoxProps>;

export default Box;
