import React from 'react';
import { default as MuiModal, ModalProps } from '@mui/material/Modal';

const Modal = React.forwardRef<HTMLElement, ModalProps>( ( props, ref ) => {
	return (
		<MuiModal
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ModalProps>;

export default Modal;
