import React from 'react';
import { default as MuiSelect, SelectProps } from '@mui/material/Select';

const Select = React.forwardRef<HTMLElement, SelectProps>( ( props, ref ) => {
	return (
		<MuiSelect
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SelectProps>;

export default Select;
