import React from 'react';
import { default as MuiDialogContent, DialogContentProps } from '@mui/material/DialogContent';

const DialogContent = React.forwardRef<HTMLElement, DialogContentProps>( ( props, ref ) => {
	return (
		<MuiDialogContent
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<DialogContentProps>;

export default DialogContent;
