import React from 'react';
import { default as MuiContainer, ContainerProps } from '@mui/material/Container';

const Container = React.forwardRef<HTMLElement, ContainerProps>( ( props, ref ) => {
	return (
		<MuiContainer
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ContainerProps>;

export default Container;
