import React from 'react';
import { default as MuiDialogContentText, DialogContentTextProps } from '@mui/material/DialogContentText';

const DialogContentText = React.forwardRef<HTMLElement, DialogContentTextProps>( ( props, ref ) => {
	return (
		<MuiDialogContentText
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<DialogContentTextProps>;

export default DialogContentText;
