import React from 'react';
import { default as MuiButtonBase, ButtonBaseProps } from '@mui/material/ButtonBase';

const ButtonBase = React.forwardRef<HTMLElement, ButtonBaseProps>( ( props, ref ) => {
	return (
		<MuiButtonBase
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ButtonBaseProps>;

export default ButtonBase;
