import React from 'react';
import { default as MuiTableContainer, TableContainerProps } from '@mui/material/TableContainer';

const TableContainer = React.forwardRef<HTMLElement, TableContainerProps>( ( props, ref ) => {
	return (
		<MuiTableContainer
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<TableContainerProps>;

export default TableContainer;
