import React from 'react';
import { default as MuiListItemAvatar, ListItemAvatarProps } from '@mui/material/ListItemAvatar';

const ListItemAvatar = React.forwardRef<HTMLElement, ListItemAvatarProps>( ( props, ref ) => {
	return (
		<MuiListItemAvatar
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<ListItemAvatarProps>;

export default ListItemAvatar;
