import React from 'react';
import clsx from 'clsx';
import Button, { ButtonProps } from '@elementor/ui/Button';
import { styled, useThemeProps, createSlots } from '@elementor/ui/styles';
import './AlertAction.types';

export interface AlertActionProps extends Omit<ButtonProps, 'variant' | 'size'> {
	variant?: Exclude<ButtonProps['variant'], 'text'>;
}

const { slots, classNames } = createSlots( 'AlertAction', [ 'root' ] );

export type AlertActionSlots = keyof typeof slots;

const Root = styled( Button, slots.root )<{ ownerState: AlertActionProps }>( {} );

const AlertAction = React.forwardRef<HTMLButtonElement, AlertActionProps>( ( inProps, ref ) => {
	const props = useThemeProps( { props: inProps, name: slots.root.name } );

	return (
		<Root
			{ ...props }
			size="small"
			ref={ ref }
			className={ clsx( [ [ classNames.root, props.className ] ] ) }
			ownerState={ props }
		/>
	);
} ) as React.FC<AlertActionProps>;

AlertAction.defaultProps = {
	color: 'inherit',
	variant: 'outlined',
};

export default AlertAction;
