import React from 'react';
import { default as MuiDialogActions, DialogActionsProps } from '@mui/material/DialogActions';

const DialogActions = React.forwardRef<HTMLElement, DialogActionsProps>( ( props, ref ) => {
	return (
		<MuiDialogActions
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<DialogActionsProps>;

export default DialogActions;
