import React from 'react';
import { default as MuiSnackbarContent, SnackbarContentProps } from '@mui/material/SnackbarContent';

const SnackbarContent = React.forwardRef<HTMLElement, SnackbarContentProps>( ( props, ref ) => {
	return (
		<MuiSnackbarContent
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<SnackbarContentProps>;

export default SnackbarContent;
