import React from 'react';
import { default as MuiStep, StepProps } from '@mui/material/Step';

const Step = React.forwardRef<HTMLElement, StepProps>( ( props, ref ) => {
	return (
		<MuiStep
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StepProps>;

export default Step;
